import React, { Component } from 'react';

class Footer extends Component {
  render() {
    require('./footer.css');
    return (

      <footer>
      <div id="footer-container">
      <p>Joe Sutliff</p>
      <div id="social-icons">
      <a href="https://github.com/joesutliff1016"><i className="fab fa-github-square"></i></a>
      <a href="https://www.linkedin.com/in/joe-sutliff-a199b0155/"><i className="fab fa-linkedin"></i></a>
      <a href="mailto:joesutliff1016@gmail.com"><i class="far fa-envelope"></i></a>
      </div>
      </div>
      </footer>

    );
  }
}

export default Footer;



