import React, { Component } from 'react';
import {NavLink} from 'react-router-dom';
import { slide as Menu } from 'react-burger-menu';


class Header extends Component {
  render() {
    require('./header.css');
    return (

      <header>
        <p>Joe Sutliff</p>
        <nav>
        <Menu right>
        <NavLink exact="true" activeClassName='is-active' className="menu-item" to="/">
        Home</NavLink>
        <NavLink activeClassName='is-active' className="menu-item" to="/Aboutpage">
        About</NavLink>
        <NavLink id="home" className="menu-item" to="/">
        Contact</NavLink>
        </Menu>
        </nav>
        </header>



    );
  }
}

export default Header;



