import React, { Component } from 'react';

class Projects extends Component {
  render() {
    require('./projects.css')
    return (

      <div id="projects">

      <h3 id="projects-h3">My Projects</h3>

      <div id="projects-grid">

      <div id="project1">
      <a href="http://blkshpnw.com/">
      <div className="project-desc">
      <p>Black Sheep Catering</p>
      <small>WEBSITE</small>
      </div>
      <div className="overlay"></div>
      </a>
      </div>

      <div id="project2">
      <a href="http://www.joesutliffwebdev.com/NorthWestSoaps/home.php">
      <div className="project-desc">
      <p>Sable</p>
      <small>WEBSITE</small>
      </div>
      <div className="overlay"></div>
      </a>
      </div>

      <div id="project3">
      <a href="https://joesutliff1016.github.io/Perfectum/">
      <div className="project-desc">
      <p>Perfectum</p>
      <small>WEBSITE</small>
      </div>
      <div className="overlay"></div>
      </a>
      </div>

      <div id="project4">
      <a href="https://joesutliff1016.github.io/Galanga/#/">
      <div className="project-desc">
      <p>Galanga Thai</p>
      <small>WEBSITE</small>
      </div>
      <div className="overlay"></div>
      </a>
      </div>

      </div>
      </div>

    );
  }
}

export default Projects;



